.MessageForm {
  display: -ms-flexbox;
  display: flex;
  background: #ccc;
}

.MessageForm .input-container {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 1px;
}

.MessageForm .button-container {
  -ms-flex: 0 0 6em;
      flex: 0 0 6em;
  margin: 1px 1px 1px 0;
}

.MessageForm input, .MessageForm button {
  background: #fff;
  border-radius: 3px;
  border: 1px solid #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: inherit;
  height: 100%;
  outline: none;
  width: 100%;
}

.MessageForm input {
  padding: 0 0 0 4px;
}



